.root {
    height: 100%;
    width: 90%;
}
.info {
    display: flex;
}

.textInfo {
    margin-left: 120px;
    padding-top: 60px;
    font-size: 48px;
}

.salary {
    margin-top: 100px;
    font-size: 48px;
}
.progress {
    margin: 120px 0;
}

.avatarSkeleton {
    border-radius: 30px;
    width: 327px;
    height: 327px;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
